import React, { useState } from 'react'
import './Banner.css'
import {Button} from '@material-ui/core'
import Search from './Search';
import { useHistory } from 'react-router-dom';

function Banner()
{
    const [showSearch,setShowSearch]=useState(false);
    const history=useHistory();
    return(
       <div className="banner">
       <div className='banner__search'>
       {showSearch && <Search />}
            
            {/* /* /* use of state to update the show and hide option*/ }
           <Button onClick={()=> setShowSearch(!showSearch)} className='search_button'
           variant='outlined'>{showSearch?"Hide":"Search Dates"}</Button>
       </div>
       <div className='banner__info'>
       <h1>Get out and stretch your imagination</h1>
            <h5>
                Plan a different kind of getaway to uncover
                the hidden gems near you.
            </h5>
            {/* //outlines the button */}
            <Button onClick={() => history.push('/search')} variant='outlined'>Explore Nearby</Button> 
       </div>  
       </div>
    )
}

export default Banner;